const ProjectContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 60vw;
  margin: auto;
  grid-row-gap: 40px;
  grid-column-gap: 40px;
  margin-top: 3rem;
`;

const GridItem = styled.div`
  font-size: 2.3rem;
  text-align: center;
  padding: 0;
  overflow: hidden;
  border-radius: 6px;

  & a {
    text-decoration: none;
  }

  & img {
    max-width: 100%;
    display: block;
    border-radius: 6px;
  }

  & p {
    color: white;
    background-color: rgb(26, 36, 54);
    margin: 0;
    padding: 20px;
  }

  & span {
    display: none;
  }

  &:hover {
    span {
      font-size: 2rem;
      font-weight: bolder;
      color: var(--neon-green);
      display: inline;
    }
    img {
      filter: blur(2px);
      transform: scale(1.05);
    }
  }
`;

