import React, { useState } from "react";
import styled from "styled-components";
import { HashLink } from 'react-router-hash-link';
import { Divide as Hamburger } from 'hamburger-react'
import logo from "../img/logo.png";
import { navigationContent } from "../constants/config";
import Button from "./layout/Button";
import './styles/index.css'


const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <header>
      <Nav>
        <Logo src={logo} alt="Company logo GDP" />
        <Hamburger className='hamburguer-icon' color={'var(--neon-green)'} toggled={isOpen} toggle={setIsOpen} />
        <Menu isOpen={isOpen}>
          {navigationContent.map((content) => (
            <ListItem>
              <SpanColor>{content.id} </SpanColor>
              <MenuLink to={`#${content.label}`}>{content.label}</MenuLink>
            </ListItem>
          ))}
          <ListItem>
            <Button>Resume</Button>
          </ListItem>
        </Menu>
      </Nav>
    </header>
  );
};

export default Navbar;

const ListItem = styled.li`
  list-style: none;
  padding-right: 1rem;
  margin-right: 1rem;
`;

const SpanColor = styled.span`
  color: var(--neon-green);
  font-size: 1.3rem;
`;

const MenuLink = styled(HashLink)`
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  color: var(--dark-grey);
  transition: all 0.3s ease-in;
  font-size: 1.6rem;
  font-family: var(--font-mono);

  &::after {
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background: var(--neon-green);
    transition: width 0.3s;
  }

  &:hover::after {
    width: 100%;
  }
  &:hover {
    color: var(--neon-green);
  }
`;

const Nav = styled.div`
  width: 100%;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background: transparent;
  top: 0;
  left: 0;
  right: 0;

  animation-duration: 0.75s;
  animation-delay: 0.5s;
  animation-name: animate-fade;
  animation-timing-function: cubic-bezier(0.26, 0.53, 0.74, 1.48);
  animation-fill-mode: backwards;
  animation-name: animate-glow;
  animation-timing-function: ease;


  @keyframes animate-glow {
  0% {
    opacity: 0;
    transform: scale(0.8, 0.8);
  }
  100% {
    opacity: 1;
    filter: brightness(1) saturate(1);
    transform: scale(1, 1);
  }
}
  /* box-shadow: 0 10px 30px -10px rgba(2, 12, 27, 0.7); */
`;

const Logo = styled.img`
  width: 5rem;
  margin-left: 2rem;

  @keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }
  &:hover {
    transform: scale(1.1);
    cursor: pointer;
    animation: rotation 2s infinite linear;
  }
`;

const Menu = styled.ul`
  display: flex;
  justify-content: space-between;
  margin-right: 3rem;
  position: relative;
  align-items: center;

 

  @media (max-width: 768px) {
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    margin: 0;
    align-self: center;
    max-height: ${({ isOpen }) => (isOpen ? "300px" : "0")};
    transition: max-height 0.3s ease-in;
    width: 100%;
  }
`;

