import React from 'react'
import styled from 'styled-components'
import GlitchText from './layout/GlitchText'
import NeonText from './layout/NeonText'
import Button from './layout/Button'
import './styles/index.css'

const SectionStyled = styled.section`
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  background-color: transparent;
  font-size: 3rem;
  height: 80vh;
  justify-content: center;

  & div:nth-child(3) {
    width: 60vw;
    text-align: center;
    margin: 2rem 2rem;
  }

  & p {
    font-size: 2rem;
    color: var(--dark-grey);
  }
  @media (max-width: 768px) {
    font-size: 1.2rem;
    padding: 0;

    & p {
      font-size: 1rem;
    }
  }
`


const Section = () => {
  return (
    <SectionStyled>
      <NeonText>Me, myself and I.</NeonText>
      <GlitchText />
      <div className='move-left'><p>A junior full stack programmer based in Buenos Aires City, Argentina
        looking forward to land a job as a web developer, both on-site or remote!</p></div>
      <Button className='move-bottom'>Contact me!</Button>
    </SectionStyled>
  )
}

export default Section
