import React from "react";
import styled from "styled-components";
// import me from "../img/me.jpeg";
import NeonText from "./layout/NeonText";
import Image from "../components/Image";
import { skillsContent } from "../constants/config";

const AboutContainer = styled.div`
  height: 85vh;

`
const StyledAbout = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 3rem;
`;

// const StyledImage = styled.div`
//   height: 30rem;
//   width: 25rem;
//   border-radius: 4px;
//   background-repeat: no-repeat;
//   background: var(--neon-green);
//   background: linear-gradient(
//       var(--neon-green-opacity),
//       var(--neon-green-opacity)
//     ),
//     url(${me}); /* The least supported option. */
//   background-size: cover;
//   transition: ease-in-out 0.6s;

//   &:hover {
//     background: url(${me});
//     background-size: cover;
//   }
// `;
const ParagraphContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 40%;
`;

const StyledParagraph = styled.p`
  font-family: var(--font-mono);
  color: var(--lightest-grey);
  font-size: 1.7rem;
`;

const StyledSkillsList = styled.ul`
  list-style: none;
  margin: auto;
  padding: 0;
  color: var(--neon-green);
  font-size: 1.5rem;
  columns: 2;
  column-gap: 10rem;
`;

const StyledLi = styled.li`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
`;

const StyledSkillsText = styled.span`
  display: inline-block;
  color: var(--white);
  padding-left: 1rem;
`;

const About = () => {
  return (
    <AboutContainer id="About">
      <NeonText size="2rem">A bit about me</NeonText>
      <StyledAbout>
        <ParagraphContainer>
          <StyledParagraph>
            Hello there! My name is Jorge and I'm a junior Full Stack web
            developer, specializing in React JS and Express JS. I don't think we
            can imagine a life without internet and websites, so I think it is
            crucial to have the best user experience possible. Currently I'm
            diving into GraphQL, Typescript and React Native.
          </StyledParagraph>
          <StyledParagraph>
            Here are a few technologies I’ve been working with recently:
          </StyledParagraph>
          <StyledSkillsList>
            {skillsContent.map((skill) => (
              <StyledLi>
                <skill.icon />
                <StyledSkillsText>{skill.label}</StyledSkillsText>
              </StyledLi>
            ))}
          </StyledSkillsList>
        </ParagraphContainer>

        <Image />
      </StyledAbout>
    </AboutContainer>
  );
};

export default About;
